import React, { useState, useCallback, useEffect } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import { useTable, useSortBy, useBlockLayout, useRowSelect } from 'react-table'
import Row from './row'
import sort from '../../images/icons/sort.svg'
import check from '../../images/icons/check.svg'
//import { useSticky } from "react-table-sticky";
import { DndProvider } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';
import update from 'immutability-helper';
import dndimg from '../../images/icons/tablednd.svg'
import editimg from '../../images/icons/edit-sm.svg'
import delimg from '../../images/icons/delete-sm.svg'
import copy from '../../images/icons/copy.svg'


const Styles = styled.div`
  /*padding: 1rem;*/

  margin-top: 20px;
  .table {
    border: 1px solid #ddd;
    width: ${props => props.smOpened ? 'calc(100vw - 500px)' : 'calc(100vw - 40px)'};

    .td__move, .td__select {
      display: none;
    }

    .td__selected {
      .td__move, .td__select {
        display: inline-block;
        position: absolute;
      }

      .td__num {
        display: none;
      }
    }

    .td__select {
      top: 13px;
      input[type="checkbox"] {
        opacity: 0;
        position: absolute;
      }

      input[type="checkbox"] + i {
        display: inline-block;
        width: 14px;
        height: 14px;
        border: 2px solid #17181A;
        background: white;
      }

      input[type="checkbox"]:checked + i {
        background: url(${check}) no-repeat center;
        background-size: 100% 100%;
        background-color: white;
      }
    }


    .header {
      box-shadow: none;
      .th {
        color: rgba(23, 24, 26, 0.5);
        font-family: 'Proxima Nova';
        font-weight: bold;
        background: #F0F0F0;
        padding: 10px;
        &:first-child {
          padding-left: 25px;
        }
        span {
          display: inline-block;
          width: 15px;
          height: 15px;
        }
        span img {
          max-width: 100%;
          margin-left: 5px;
  
          &.sort__down {
            transform: rotate(180deg);
          }
        }
      }

      .td__select {
        display: inline-block;
        position: absolute;
        left: 10px;
      }
    }

    .tr {
      :last-child {
        .td {
          border-bottom: 0;
        }
      }
    }

    

    .tr__hover {
      transition: 0.2s;
      &:hover {
        .td {
          background: linear-gradient(0deg, rgba(0, 76, 186, 0.07), rgba(0, 76, 186, 0.07)), #FFFFFF;
        }

        .tb__id {
          .td__move, .td__select {
            display: inline-block;
            position: absolute;
          }

          .td__num {
            display: none;
          }
        }
      }
    }

    .th,
    .td {
      padding: 10px;
      border-bottom: 1px solid #D8D8D8;
      border-right: 1px solid #D8D8D8;
      background-color: #fff;
      overflow: hidden;
      font-family: HSESans;
      font-size: 80%;
      line-height: 1.8;
      color: #17181A;

      align-items: center;
      position: relative;


      .resizer {
        display: inline-block;
        width: 5px;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
        transform: translateX(50%);
        z-index: 1;

        &.isResizing {
          background: red;
        }
      }
    }

    .td__edit {
      color: #004CBA;
      position: relative;
      cursor: pointer;
      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        width: 20px;
        height: 20px;
        background: url(${editimg}) no-repeat center;
        top: 10px;
      }
    }

    .td__del {
      color: #D0021B;
      position: relative;
      cursor: pointer;
      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        width: 20px;
        height: 20px;
        background: url(${delimg}) no-repeat center;
        top: 10px;
      }
    }

    .td__copy {
      color: #004CBA;
      position: relative;
      cursor: pointer;
      &:after {
        content: '';
        display: inline-block;
        position: absolute;
        width: 20px;
        height: 20px;
        background: url(${copy}) no-repeat center;
        top: 10px;
      }
    }

    &.sticky {
      overflow: auto;
      max-height: 100vh;
      .header,
      .footer {
        position: sticky;
        z-index: 1;
        width: fit-content;
      }

      .header {
        top: 0;
      }


      .body {
        position: relative;
        z-index: 0;
      }

      [data-sticky-td] {
        position: sticky;
      }

      [data-sticky-last-left-td] {
        box-shadow: 2px 0px 3px #ccc;
      }

      [data-sticky-first-right-td] {
        box-shadow: -2px 0px 3px #ccc;
      }
    }


    .td__move {
      position: absolute;
      z-index: 10;
      width: 9px;
      height: 16px;
      background: url(${dndimg}) no-repeat center;
      left: 10px;
      top: 11px;
      cursor: move;
    }

    .tb__id {
      padding-left: 30px;
      width: 40px;
    }
  }

  .zebra {
    display: inline-block;
    .tr:nth-child(2n) {
      .tr__hover .td {
        background: linear-gradient(0deg,rgba(0,76,186,0.07),rgba(0,76,186,0.07)),#FFFFFF;
      }
    }
  }

  .noselect {
    .tr__hover {
      transition: 0.2s;
      &:hover {
        .tb__id  .td__num {
          display: inline-block;
        }
      }
    } 

    .header .td__select {
      display: none;
    }
  }
`;


const CntxMenu = styled.div`
  position: absolute;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  left: ${props => props.x}px;
  top: ${props => props.y}px;
  z-index: 15;
  min-width: 320px;
  ul {
    li {
      display: block;
      padding: 20px 30px;
      cursor: pointer;
      transition: 0.2s;
      border-bottom: 1px solid #D1D1D1;
      font-family: 'Proxima Nova';
      font-style: normal;
      font-weight: normal;
      font-size: 120%;
      line-height: 1.2;
      color: #8B8B8B;
      &:hover {
        background: #F0F0F0;
      }

      &:last-child {
        border-bottom: none;
      }

      &.active {
        color: #17181A;
        background: #F0F0F0;
      }
    }
  }
`


function Table({ columns, data, smOpened, sorting, select, dnd }) {

  const [records, setRecords] = useState(data)
  const [menu, openMenu] = useState({visible: false, x: 0, y: 0});
  const [zebra, setZebra] = useState(false);
  const tableRef = React.useRef(null)

  const getRowId = useCallback(row => {
    return row.id
  }, [])
  

  const defaultColumn = React.useMemo(
    () => ({
      minWidth: 150,
      maxWidth: 500
    }),
    []
  );
  // Use the state and functions returned from useTable to build your UI
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    selectedFlatRows,
    state: { selectedRowIds },
  } = useTable(
    {
      columns,
      data: records,
      defaultColumn,
      getRowId
    },
    useBlockLayout,
    // useSticky,
    useSortBy,
    useRowSelect,
    hooks => {
      hooks.visibleColumns.push(columns => [
        ...columns,
      ])
    }
  );


  const moveRow = (dragIndex, hoverIndex) => {
    const dragRecord = records[dragIndex]
    setRecords(
      update(records, {
        $splice: [
          [dragIndex, 1],
          [hoverIndex, 0, dragRecord],
        ],
      })
    )
  }

  const isSortedTable = sorting ? (column) => column.getSortByToggleProps() : () => '';

  

  function contextMenuInit(event) {
    event.preventDefault();
    console.log(document.body.scrollTop)
    const clickX = event.clientX;
    const clickY = event.clientY + document.documentElement.scrollTop;
    openMenu({ visible: true, x: clickX, y: clickY });
  };

  
  function contextMenuClose(event) {
    openMenu({ visible: false, x:0, y:0 });
  };

  function contextMenuToggle(currtable) {
    currtable.addEventListener('contextmenu', contextMenuInit);
    document.addEventListener('click', contextMenuClose);
  }

  useEffect(() => {
    const currtable = tableRef.current
    contextMenuToggle(currtable);
    console.log(currtable.offsetTop);  
    return () => {
      currtable.removeEventListener('contextmenu', contextMenuInit);
      document.removeEventListener('click', contextMenuClose);
    };
  }, []);



  // Render the UI for your table
  return (
    
      <Styles smOpened={smOpened}>
        <div
          {...getTableProps()}
          className={`table sticky ${zebra ? 'zebra' : ''} ${select ? '' : 'noselect'}`}
          onContextMenu={(e) => e.preventDefault()}
          ref={tableRef}
        >
          {console.log(window.scrollY)}
          {menu.visible ?
            <CntxMenu x={menu.x} y={menu.y}>
              <ul>
                <li>
                  Удалить
                </li>
                <li>
                  Версия для печати
                </li>
                <li className={zebra ? 'active' : ''} onClick={() => setZebra(!zebra)}>
                  {zebra ? 'Выключить зебру' : 'Включить зебру'}
                </li>
              </ul>
            </CntxMenu>
          : ''}
          <div className="header">
            {headerGroups.map((headerGroup, i) => {
              
              /*
              headerGroup.headers[0].totalWidth = headerGroup.headers[0].width
              headerGroup.headers[0].totalLeft = headerGroup.headers[0].totalLeft2

              headerGroup.headers[1].totalWidth = headerGroup.headers[1].width
              headerGroup.headers[1].totalLeft = headerGroup.headers[1].totalLeft2

              headerGroup.headers[2].totalWidth = headerGroup.headers[2].width
              */

              return <div {...headerGroup.getHeaderGroupProps()} className="tr">
                {headerGroup.headers.map((column, i) => {
                  column.totalWidth = column.width

                  if (column.sticky) {
                    column.totalLeft = column.totalLeft2
                  }


                  return <div {...column.getHeaderProps(isSortedTable(column))}  className={`th`}>
                    {column.render("Header")}
                    <span>
                      {column.isSorted && sorting
                        ? column.isSortedDesc
                          ?  <span>
                                <img src={sort} alt="sort" className="sort__down" />
                              </span>
                          : <span>
                              <img src={sort} alt="sort" />
                            </span>
                        : ''}
                    </span>
                  </div>
                })}
              </div>
            })}
          </div>
          <div {...getTableBodyProps()} className="body">
            {rows.map((row, i) => {
              prepareRow(row);
              return (
                <div {...row.getRowProps()} className="tr">
                      <Row
                        index={i}
                        row={row}
                        selectedrows={Object.keys(selectedRowIds)}
                        moveRow={moveRow}
                        isEnableSelect={select}
                        isEnableMove={dnd}
                        {...row.getRowProps()}
                      />
                </div>
              );
            })}
          </div>

        </div>
      </Styles>
    
  )
}




Table.propTypes = {

}



const mapStateToProps = store => {
  return {
    smOpened: store.togglesidemenu.isOpened,
  }
}


export default connect(mapStateToProps)(Table)


