import React from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import BlueButton from '../tables/blueButton'
import more from '../../images/icons/more.svg'
import arr from '../../images/icons/arrselect.svg'
import Select from 'react-select';
//import SideMenuKKK from './sidemenu/mainkkk';
//import Header from './header/main'
/*
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch,
  useLocation 
} from "react-router-dom";
*/

const StyledWrapper = styled.section`

`;



const ButtonMore = styled.div`
    display: inline-block;
    background: #F0F0F0;
    vertical-align: top;
    position: relative;
    padding: 11px 28px;
    transition: 0.2s;
    cursor: pointer;
    outline: none;
    border: none;
    margin-left: 15px;
    font-family: 'Proxima Nova';
    font-style: normal;
    font-weight: normal;
    font-size: 110%;
    line-height: 1.6;

    &:hover, &:focus {
        background: #D8D8D8;
    }
`


const Buttons = styled.div`
    margin-bottom: 20px;
`

const StyledInputs = styled.div`


  .filter__showed, .filter__hidden {
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;

    @media(max-width: 1500px) {
        grid-template-columns: repeat(2, 1fr);
    }
  }

  .filter__select__menu {
      z-index: 10;
  }

  .filter__select__control {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.8;
    padding: 1px;
    border-radius: 0px;
    color: #8B8B8B;
  }

  .filter__select__dropdown-indicator {
    svg {
      display: none;
    }
    &:before {
      content: '';
      display: block;
      width: 30px;
      height: 30px;
      background: url(${arr}) no-repeat center;
    }
  }

  .filter__select__indicator-separator {
    display: none;
  }

  .filter__select__option {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.5;
    padding: 10px;
    &:active {
      background-color: #EEF4FF;
    }
  }

  .filter__select__option--is-selected {
    background-color: #EEF4FF;
    color: black;
  }

  .filter__select__single-value {
    color: #8B8B8B;
  }

  input[type="text"] {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.8;
    padding: 10px;
    display: inline-block;
    border: 1px solid #D8D8D8;
    width: 100%;
    transition: 0.2s;
    &:focus {
      border: 1px solid #D8D8D8;
      outline: none;
    }
  }

  textarea {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.8;
    padding: 9px;
    display: inline-block;
    border: 1px solid #D8D8D8;
    width: 100%;
    height: 276px;
    outline: none;
    resize: none;
  }

  label, 
  .comp__select {
    display: block;
    margin-bottom: 40px;
  }

  .form-name {
    font-family: 'Proxima Nova';
    font-size: 120%;
    line-height: 1.4;
    color: #17181A;
    font-weight: 700;
  }
`;

const FormWrap = styled.div`
  padding-bottom: 30px;
  margin-bottom: 190px;
  border-bottom: 1px solid #D8D8D8;
`

const options = [
  { value: 'test1', label: 'Школа дизайна' },
  { value: 'test2', label: '2019/2020' },
  { value: 'test3', label: 'Все направления' },
  { value: 'test4', label: 'Все типы обучения' }
]




function MakeCompetencePage(props) {
  return (
    <StyledWrapper>
        <div className="container-fluid">
          <FormWrap>
          <div className="row">
            <div className="col-12">
              <h1>Новая компетенция</h1>
            </div>
            <div className="col-12">
              <Buttons>
                <BlueButton />
                <ButtonMore>Закрыть</ButtonMore>
              </Buttons>
            </div>
            <div className="col-xl-6 col-lg-6 col-12">
              <StyledInputs>
                <label>
                  <div className="form-name">
                    Название
                  </div>
                  <input type="text" />
                </label>
                <div className="comp__select">
                  <div className="form-name">
                    Тип
                  </div>
                  <Select defaultValue={options[0]} classNamePrefix={'filter__select'} options={options} />
                </div>
                <label>
                  <div className="form-name">
                    Приобретая данную компетенцию, обучающийся должен:
                  </div>
                  <textarea></textarea>
                </label>
                <div className="comp__select">
                  <div className="form-name">
                    Преподаватели
                  </div>
                  <Select 
                    defaultValue={[options[0], options[1]]} 
                    isMulti 
                    classNamePrefix={'filter__select'} 
                    options={options} 
                    placeholder={'Выберите преподавателей'}
                    />
                </div>
              </StyledInputs>
            </div>
            <div className="col-xl-6 col-lg-6 col-12">
              <StyledInputs>
                <div className="comp__select">
                  <div className="form-name">
                    Дисциплина
                  </div>
                  <Select defaultValue={options[0]} classNamePrefix={'filter__select'} options={options} />
                </div>
                <div className="comp__select">
                  <div className="form-name">
                    Количество кредитов
                  </div>
                  <Select defaultValue={options[0]} classNamePrefix={'filter__select'} options={options} />
                </div>
                <label>
                  <div className="form-name">
                    Пример работ А
                  </div>
                  <input type="text" placeholder="9999"/>
                </label>
                <label>
                  <div className="form-name">
                    Пример работ Б
                  </div>
                  <input type="text" placeholder="9999"/>
                </label>
                <label>
                  <div className="form-name">
                    Пример работ В
                  </div>
                  <input type="text" placeholder="9999"/>
                </label>
                <div className="comp__select">
                  <div className="form-name">
                    Отметить звездой
                  </div>
                  <Select defaultValue={options[0]} classNamePrefix={'filter__select'} options={options} />
                </div>
              </StyledInputs>
            </div>
          </div>
          </FormWrap>
        </div>
    </StyledWrapper>
  );
}


MakeCompetencePage.propTypes = {

}


const mapStateToProps = store => {
  return {

  }
}

export default connect(mapStateToProps)(MakeCompetencePage)


