import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import Select from 'react-select';
import arr from '../../images/icons/arrselect.svg'
import fimage from '../../images/icons/filter.svg'
import more from '../../images/icons/more.svg'
import Toggle from 'react-toggle'



const options = [
    { value: 'test1', label: 'Все факультеты' },
    { value: 'test2', label: '2019/2020' },
    { value: 'test3', label: 'Все направления' },
    { value: 'test4', label: 'Все типы обучения' }
  ]

  let testdata = [
    {
        options: options, 
        defaultValue: options[0],
        media: 4000
    },
    {
        options: options, 
        defaultValue: options[1], 
        media: 1500
    },
    {
        options: options, 
        defaultValue: options[2], 
        media: 1500
    },
    {
        options: options, 
        defaultValue: options[2],
        media: 500
    },
    {
        options: options, 
        defaultValue: options[0], 
        media: 500
    },
]


const Wrap = styled.div`
  display: flex;
`


const StyledFiltersSelect = styled.div`
  flex: 1;

  .filter__showed, .filter__hidden {
    display: grid;
    grid-template-columns: repeat(4, 1fr);

    @media(max-width: 1500px) {
        grid-template-columns: repeat(2, 1fr);
    }
  }

  .filter__select__menu {
      z-index: 10;
  }

  .filter__select__control {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.8;
    padding: 1px;
    border-radius: 0px;
    color: #8B8B8B;
  }

  .filter__select__dropdown-indicator {
    svg {
      display: none;
    }
    &:before {
      content: '';
      display: block;
      width: 30px;
      height: 30px;
      background: url(${arr}) no-repeat center;
    }
  }

  .filter__select__indicator-separator {
    display: none;
  }

  .filter__select__option {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.5;
    padding: 10px;
    &:active {
      background-color: #EEF4FF;
    }
  }

  .filter__select__option--is-selected {
    background-color: #EEF4FF;
    color: black;
  }

  .filter__select__single-value {
    color: #8B8B8B;
  }
`;

const SelectWrap = styled.div`
  margin-bottom: 20px;
  margin-right: 20px;
`;

const StyledFiltersButtons = styled.div`
  width: 230px;
  position: relative;
`

const ButtonFilter = styled.button`
    display: inline-block;
    position: relative;
    box-shadow: none;
    font-family: Proxima Nova;
    font-style: normal;
    font-weight: normal;
    font-size: 110%;
    line-height: 1.5;
    padding: 12px 30px;
    padding-left: 50px;
    background: #F0F0F0;
    transition: 0.2s;
    outline: none;
    border: none;
    margin-right: 20px;
    vertical-align: middle;
    &:before {
        content: '';
        position: absolute;
        width: 24px;
        height: 24px;
        background: url(${fimage}) no-repeat center;
        top: calc(50% - 12px);
        left: 20px;
    }
    &:hover, &:focus {
        background: #D8D8D8;
    }
`

const ButtonMore = styled.div`
    display: inline-block;
    background: #F0F0F0;
    vertical-align: middle;
    position: relative;
    height: 50px;
    width: 70px;
    transition: 0.2s;
    cursor: pointer;
    outline: none;
    border: none;
    &:before {
        content: '';
        position: absolute;
        width: 24px;
        height: 24px;
        background: url(${more}) no-repeat center;
        top: calc(50% - 10px);
        left: 24px;
    }

    &:hover, &:focus {
        background: #D8D8D8;
    }
`

const MoreMenu = styled.div`
    display: ${props => props.isOpen ? 'block' : 'none'};
    position: absolute;
    background: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
    left: -170px;
    top: 50px;
    z-index: 15;
    min-width: 400px;
    ul {
    li {
        display: block;
        padding: 20px 30px;
        cursor: pointer;
        transition: 0.2s;
        border-bottom: 1px solid #D1D1D1;
        font-family: 'Proxima Nova';
        font-style: normal;
        font-weight: normal;
        font-size: 120%;
        line-height: 1.2;
        color: #17181A;
        &:hover {
        background: #F0F0F0;
        }

        &:last-child {
        border-bottom: none;
        }

        &.active {
        color: #17181A;
        background: #F0F0F0;
        }
    }
    }

    .react-toggle {
        position: relative;
        top: 5px;
        margin-right: 20px;
    }

    label {
        display: block;
        margin-bottom: 0;
        cursor: pointer;
    }
`

function FiltersSelect(props) {
    const [moreMenu, toggleMoreMenu] =  useState(false);
    const [moreFilter, toggleMoreFilter] =  useState(false);
    const [cheeseIsReady, handleCheeseChange] = useState(false)


    const wdth = window.innerWidth;
    let showedFiltersData = testdata.filter(item => item.media < wdth)
    let hiddenFiltersData = testdata.filter(item => item.media > wdth)


    let showedFilter = showedFiltersData.map((item, i) => 
        <SelectWrap  key={i}>
            <Select defaultValue={item.defaultValue} classNamePrefix={'filter__select'} options={item.options} />
        </SelectWrap>
    )

    let hiddenFilter = hiddenFiltersData.map((item, i) => 
        <SelectWrap  key={i}>
            <Select key={i} defaultValue={item.defaultValue} classNamePrefix={'filter__select'} options={item.options} />
        </SelectWrap>
    )

      return (
        <Wrap>
          <StyledFiltersSelect>
              <div className="filter__showed">
                {showedFilter}
              </div>
              <div className="filter__hidden">
                 {moreFilter ? hiddenFilter : ''}
              </div>
          </StyledFiltersSelect>
          <StyledFiltersButtons>
            <ButtonFilter onClick={(e) => toggleMoreFilter(!moreFilter)}>Фильтр</ButtonFilter>
            <ButtonMore onClick={(e) => toggleMoreMenu(!moreMenu)}>
            </ButtonMore>
            <MoreMenu isOpen={moreMenu}>
                    <ul>
                        <li>
                            Добавить 
                        </li>
                        <li>
                            Просмотр изменений в составах групп 
                        </li>
                        <li>
                            Перенос студентов между группами 
                        </li>
                        <li>
                            <label htmlFor='cheese-status'>
                                <Toggle
                                    id='cheese-status'
                                    defaultChecked={cheeseIsReady}
                                    onChange={() => handleCheeseChange(!cheeseIsReady)} 
                                />
                                Занятия других недель
                            </label>
                        </li>
                    </ul>
                </MoreMenu>
          </StyledFiltersButtons>
        </Wrap>
      );
  }
  


FiltersSelect.propTypes = {
}


const mapStateToProps = store => {
    return {
      //meridian: store.meridian,
      //center: store.countryListByCoordinate.coordinates[1],
      //viewMode: store.mapMode.mode
    }
  }
  
  export default connect(mapStateToProps)(FiltersSelect)



