import React, { useState } from 'react';
import styled from "styled-components";
import Select from 'react-select';
import { connect } from 'react-redux';
import { authLogin } from '../../actions/authlogin' 
import arr from '../../images/icons/arrselect.svg'
import { Route, Redirect, Link } from "react-router-dom";


const options = [
    { value: 'test1', label: 'Школа дизайна' },
    { value: 'test2', label: '2019/2020' },
    { value: 'test3', label: 'Все направления' },
    { value: 'test4', label: 'Все типы обучения' }
  ]



const LoginFormContainer = styled.div`
    label, .login__select {
        display: block;
        margin-bottom: 45px;
    }

    .login__form-name {
        font-family: 'Proxima Nova';
        font-size: 150%;
        line-height: 1.4;
        color: #17181A;
        font-weight: 700;
    }


    .filter__select__menu {
        z-index: 10;
    }
  
    .filter__select__control {
      font-family: 'Proxima Nova';
      font-size: 100%;
      line-height: 1.8;
      padding: 6px;
      border-radius: 0px;
      color: #8B8B8B;
    }
  
    .filter__select__dropdown-indicator {
      svg {
        display: none;
      }
      &:before {
        content: '';
        display: block;
        width: 30px;
        height: 30px;
        background: url(${arr}) no-repeat center;
      }
    }
  
    .filter__select__indicator-separator {
      display: none;
    }
  
    .filter__select__option {
      font-family: 'Proxima Nova';
      font-size: 100%;
      line-height: 1.5;
      padding: 10px;
      &:active {
        background-color: #EEF4FF;
      }
    }
  
    .filter__select__option--is-selected {
      background-color: #EEF4FF;
      color: black;
    }
  
    .filter__select__single-value {
      color: #8B8B8B;
    }
  
    input[type="text"],
    input[type="password"] {
        width: 100%;
      font-family: 'Proxima Nova';
      font-size: 100%;
      line-height: 1.8;
      padding: 15px;
      display: inline-block;
      padding-right: 30px;
      border: 1px solid #D8D8D8
    }

    .login__forget {
        margin-bottom: 40px;
        font-family: HSESans;
        font-size: 150%;
        line-height: 1.5;
        a {
            color: #004CBA
        }
    }

    input[type="submit"] {
        display: inline-block;
        padding: 20px 110px;
        text-align: center;
        letter-spacing: 2px;
        text-transform: uppercase;
        background: #17181A;
        color: white;
        line-height: 1.2;
        outline: none;
    }

    .login__submit {
        display: flex;
        justify-content: flex-end;
    }

`

function LoginForm(props) {
    const [login, setLogin] =  useState('');
    const [pass, setPass] = useState('');
    const { authLogin, auth } = props;

    const loginsubmit = (e) => {
        e.preventDefault();
        authLogin(login, pass)
    }

    return(
        <LoginFormContainer>
            <div className="container">
                <div className="row">
                    <div className="offset-xl-2 col-xl-8 offset-lg-2 col-lg-8 col-12">
                        <div className="login__form">
                            <form name="login">
                                <h1>
                                    Вход
                                </h1>
                                <label>
                                    <div className="login__form-name">
                                        E-mail
                                    </div>
                                    <input type="text" value={login} onChange={(e) => setLogin(e.target.value)}/>
                                </label>
                                <label>
                                    <div className="login__form-name">
                                        Пароль
                                    </div>
                                    <input type="password" value={pass} onChange={(e) => setPass(e.target.value)}/>
                                </label>
                                <div className="login__select">
                                    <div className="login__form-name">
                                        Организация
                                    </div>
                                    <Select defaultValue={options[0]} classNamePrefix={'filter__select'} options={options} />
                                </div>
                                <div className="login__forget">
                                    Забыли пароль? <a href="#">Сбросить пароль</a>
                                </div>
                                <div className="login__submit">
                                    <input type="submit" value="Войти" onClick={(e) => loginsubmit(e)}/>
                                </div>
                            </form>
                            <div className="messages">
                                {auth.isLoading ? 'Загрузка...' : ''}
                                {auth.data.resultCode === 1 ? 
                                    'Такого пользователя не существует' : 
                                    auth.data.resultCode === 2 ? 
                                    'Пароль неверный' : 
                                    auth.data.resultCode === 100 ? 
                                    '' : ''
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </LoginFormContainer>
    )
}

LoginForm.propTypes = {
  
}



const mapStateToProps = store => {
  return {
    auth: store.auth
  }
}

const mapDispatchToProps = dispatch => {
    return {
      authLogin: (login, password) => dispatch(authLogin(login, password)),
    }
  }



export default connect(mapStateToProps, mapDispatchToProps)(LoginForm)


