import React, { useState } from 'react';
import Table from '../tables/main'
import { IndeterminateCheckbox } from '../tables/checkbox'
import makeData from '../tables/makeData'
import { connect } from 'react-redux';
import styled from "styled-components";
import FiltersSelect from '../filters/main'
import BlueButton from '../tables/blueButton';


const TableWrapper = styled.section`
  margin-top: 40px;

  table {
    border-spacing: 0;
    width: 100%;
    }

    tbody {
      ${'' /* These styles are required for a scrollable table body */}
      overflow-y: scroll;
      overflow-x: hidden;
      height: 250px;
    }
  }

  .testing {
    label {
      display: inline-block;
      margin-right: 20px;
    }
  }
`;

function TagsPage(props) {
    const { smOpened } = props;
  
    const [sort, setSort] = useState(false);
    const [dnd, setDnd] = useState(false);
    const [select, setSelect] = useState(false);
  
    const columns = React.useMemo(
      () => [
            {
              Header: ({ getToggleAllRowsSelectedProps }) => (
                <div className="td__select">
                  <label>
                    <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
                    <i></i>
                  </label>
                </div>
              ),  
              accessor: 'id',
              className: 'width__test',
              width: 80
            },
            {
              Header: 'Название',
              accessor: 'groupName',
              width: 220
            },
            {
              Header: '',
              accessor: 'edit',
            },
            {
              Header: '',
              accessor: 'del',
            },
            
      ],
      []
    )
  
    const data = React.useMemo(() => makeData(20), [])
    
    return (
      <TableWrapper>
        <div className="container-fluid">
          <h1>Теги компетенций</h1>
          <div className="testing">
            <label>
              <input type="checkbox" checked={sort} onChange={() => setSort(!sort)}/>
              Сортировка
            </label>
            <label>
              <input type="checkbox" checked={select} onChange={() => setSelect(!select)}/>
              Выделение
            </label>
            <label>
              <input type="checkbox" checked={dnd} onChange={() => setDnd(!dnd)}/>
              ДнД
            </label>
          </div>
          <div>
            <BlueButton />
          </div>
          <Table 
            columns={columns} 
            data={data} 
            smOpened={smOpened} 
            sorting={sort}
            select={select}
            dnd={dnd}
          />
        </div>
      </TableWrapper>
    )
  }
  
  
  
  TagsPage.propTypes = {
  
  }
  
  
  
  const mapStateToProps = store => {
    return {
      smOpened: store.togglesidemenu.isOpened,
    }
  }
  
  
  export default connect(mapStateToProps)(TagsPage)


