import React from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import SideMenuKKK from './sidemenu/mainkkk';
import Header from './header/main'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  useParams,
  useRouteMatch,
  useLocation 
} from "react-router-dom";
import TagsPage from './pages/tags'
import ProgramsPage from './pages/programs'
import CompetencePage from './pages/competence'
import MakeCompetencePage from './pages/competencemake'


const StyledWrapper = styled.section`
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
`;


const MainContent = styled.section`
  flex: 1; 
`;



function WrapperKKK(props) {
  const { smOpened } = props;
  let { path, url } = useRouteMatch();
  return (
    <StyledWrapper>
        <SideMenuKKK />
        <MainContent>
          <Header />
          <Switch>
            <Route exact path={path}>
                <div>Карта Креативных Компетенций</div>
            </Route>
            <Route exact path={`${path}/compеtence`}>
              <CompetencePage />
            </Route>
            <Route exact path={`${path}/compеtence/new`}>
              <MakeCompetencePage />
            </Route>
            <Route path={`${path}/prog`}>
              <ProgramsPage />
            </Route>
            <Route path={`${path}/tags`}>
              <TagsPage />
            </Route>
          </Switch>
        </MainContent>
    </StyledWrapper>
  );
}



WrapperKKK.propTypes = {

}


const mapStateToProps = store => {
  return {

  }
}

export default connect(mapStateToProps)(WrapperKKK)


