
import { getPrograms } from './getprograms'
export const SET_PROGRAM_ID = 'SET_PROGRAM_ID'

export function setProgramId(id) {
  return (dispatch) => {

    dispatch({
      type: SET_PROGRAM_ID,
      payload: id,
    })

    dispatch(getPrograms(id));

  }
}


