import React from 'react';
import { useDrag, useDrop } from 'react-dnd';
import { IndeterminateCheckbox } from './checkbox'



const DND_ITEM_TYPE = 'row'

export default function Row({ row, index, moveRow, selectedrows, isEnableSelect, isEnableMove }) {
  const dropRef = React.useRef(null)
  const dragRef = React.useRef(null)

  const [, drop] = useDrop({
    accept: DND_ITEM_TYPE,
    hover(item, monitor) {
      if (!dropRef.current) {
        return
      }
      const dragIndex = item.index
      const hoverIndex = index
      // Don't replace items with themselves
      if (dragIndex === hoverIndex) {
        return
      }
      // Determine rectangle on screen
      const hoverBoundingRect = dropRef.current.getBoundingClientRect()
      // Get vertical middle
      const hoverMiddleY =
        (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2
      // Determine mouse position
      const clientOffset = monitor.getClientOffset()
      // Get pixels to the top
      const hoverClientY = clientOffset.y - hoverBoundingRect.top
      // Only perform the move when the mouse has crossed half of the items height
      // When dragging downwards, only move when the cursor is below 50%
      // When dragging upwards, only move when the cursor is above 50%
      // Dragging downwards
      if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
        return
      }
      // Dragging upwards
      if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
        return
      }
      // Time to actually perform the action
      moveRow(dragIndex, hoverIndex)
      // Note: we're mutating the monitor item here!
      // Generally it's better to avoid mutations,
      // but it's good here for the sake of performance
      // to avoid expensive index searches.
      item.index = hoverIndex
    },
  })

  function rowSelectedCheck() {
    for (let i=0; i < selectedrows.length; i++) {
      if (row.id === +selectedrows[i]) {
        return true;
      }
    }
    return false;
  }


  const [{ isDragging }, drag, preview] = useDrag({
    item: { type: DND_ITEM_TYPE, index },
    collect: monitor => ({
      isDragging: monitor.isDragging(),
    }),
  })

  const opacity = isDragging ? 0 : 1
  const isRowSelected = rowSelectedCheck();

  preview(drop(dropRef))
  drag(dragRef)



  return (
    <div  ref={dropRef} className="tr__hover" style={{ opacity, display: 'flex' }}>
      {row.cells.map((cell, i) => {
      if (i === 0) {
        return  <div {...cell.getCellProps()}
                    className={`td tb__id ${isRowSelected && isEnableSelect ? 'td__selected' : ''}`}>
                    {isEnableMove ? <div className='td__move'  ref={dragRef}></div> : ''}
                    <span className="td__num">
                      {cell.render('Cell')}
                    </span>
                    {isEnableSelect ?
                    <label className="td__select">
                      <IndeterminateCheckbox {...row.getToggleRowSelectedProps()} />
                      <i></i>
                    </label> : ''
                    }
                </div>
      }

      if (cell.column.id === 'edit') {
        return <div {...cell.getCellProps()}    className="td td__edit">Редактировать</div>
      }

      if (cell.column.id === 'del') {
        return <div {...cell.getCellProps()}   className="td td__del">Удалить</div>
      }

      if (cell.column.id === 'copy') {
        return <div {...cell.getCellProps()}   className="td td__copy">Копировать</div>
      }

        return <div {...cell.getCellProps()}  style={{ ...cell.getCellProps().style }}  className="td">{cell.render('Cell')}</div>
      })}
    </div>
  )
}


