import React, { useState } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  NavLink,
  useLocation,
  useRouteMatch
} from "react-router-dom";
import tri from '../../images/icons/tri.svg'

const Item = styled.div`
  margin-bottom: 20px;
`;

export const Title = styled.div`
  position: relative;
  padding-left: 40px;
  padding-right: 20px;
  text-transform: uppercase;
  color: #8B8B8B;
  margin-bottom: 8px;
  cursor: pointer;
  &:before {
    content: '';
    width: 14px;
    height: 14px;
    left: 20px;
    position: absolute;
    display: block;
    background: url(${tri}) no-repeat center;
    transform: rotate(${props => props.opened ? '0deg' : '-90deg'})
  }
`;

const SubTitle = styled.div`
  position: relative;
  padding-left: 40px;
  padding-right: 20px;
  margin-bottom: 8px;
  cursor: pointer;
  &:before {
    content: '';
    width: 14px;
    height: 14px;
    left: 20px;
    position: absolute;
    display: block;
    background: url(${tri}) no-repeat center;
    transform: rotate(${props => props.opened ? '0deg' : '-90deg'})
  }
`;


export const List = styled.div`
  display: ${props => props.opened ? 'block' : 'none'};
  ul {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
  }

  li a {
    display: block;
    padding-left: 60px;
    padding-right: 20px;
    padding-top: 8px;
    padding-bottom: 8px;
    line-height: 1.5;
    transition: 0.2s;
    color: #17181A;
    &:hover {
      background: #D8D8D8;
      font-weight: bold;
      font-family: 'Proxima Nova';
    }
  }

  li a.active {
    background: #D8D8D8;
    font-weight: bold;
    font-family: 'Proxima Nova';
  }
`;


function MenuItem(props) {
  const [opened, setOpened] = useState(true);
  const { title, list, isInner } = props;
  let { path, url } = useRouteMatch();

  let location = useLocation();

  const linksList = list.map((item, i) => <li key={i}>
    <NavLink activeClassName={'active'} to={`${url}/${item.link}`}>
      {item.name}
    </NavLink>
  </li>);
  
  /* меню с вложенными внутренними списками */
  if (isInner) {
    return (
      <React.Fragment>
        <SubTitle onClick={() => setOpened(!opened)} opened={opened}>
          {title} 
        </SubTitle>
        <List actpath={location.pathname} opened={opened}>
          <ul>
            {linksList}
          </ul>
        </List>
      </React.Fragment>
    )
  }




  return (
    <Item>
      <Title onClick={() => setOpened(!opened)} opened={opened}>
        {title} 
      </Title>
      <List actpath={location.pathname} opened={opened}>
        <ul>
          {linksList}
        </ul>
      </List>
    </Item>
  );
}



MenuItem.propTypes = {

}


/*
const mapStateToProps = store => {
  return {
    //meridian: store.meridian,
    //center: store.countryListByCoordinate.coordinates[1],
    //viewMode: store.mapMode.mode
  }
}
*/

export default connect()(MenuItem)


