import React from 'react';
import { Provider, connect } from 'react-redux';
import { BrowserRouter as Router, Route, Link, Switch, useRouteMatch, Redirect } from "react-router-dom";
import Main from './containers/Main';
import MainKKK from './containers/MainKKK';
import Login from './containers/Login';
import ProtectedRoute from "./containers/ProtectedRoute";




function App(props) {
  let { url, path } = useRouteMatch();
  const { authinfo } = props;
  let isAuthenticated = authinfo.status === 200 ? true : false;

  return(
    <div>
        <Route exact path={path}>
          <Link to={`${path}main`}>Меню: таймтрекер</Link>
        </Route>
        <ProtectedRoute path={`${path}main`} component={Main}   isAuthenticated={isAuthenticated} isVerifying={authinfo.isLoading}/>
        <ProtectedRoute path={`${path}kkk`} component={MainKKK} isAuthenticated={isAuthenticated} isVerifying={authinfo.isLoading}/>
        <Route path={`${path}login`}>
          {isAuthenticated && !authinfo.isLoading ? <Redirect to={'/'} /> : <Login />}
        </Route>
    </div>
  )
}

const mapStateToProps = store => {
  return {
    authinfo: store.authinfo,
  }
}

const mapDispatchToProps = dispatch => {
  return {

  }
}


export default connect(mapStateToProps)(App)





