import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import Select from 'react-select';
import arr from '../../images/icons/arrselect.svg'
import search from '../../images/icons/seach.svg'
import Toggle from 'react-toggle'
import BlueButton from '../tables/blueButton';
import { setProgramId } from '../../actions/setProgramId'
import { options, optionsyear } from '../../constants/kkkprograms'



const Wrap = styled.div`
  display: flex;
`


const StyledFiltersSelect = styled.div`
  flex: 1;

  .filter__showed, .filter__hidden {
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;

    @media(max-width: 1500px) {
        grid-template-columns: repeat(2, 1fr);
    }
  }

  .filter__select__menu {
      z-index: 10;
  }

  .filter__select__control {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.8;
    padding: 1px;
    border-radius: 0px;
    color: #8B8B8B;
  }

  .filter__select__dropdown-indicator {
    svg {
      display: none;
    }
    &:before {
      content: '';
      display: block;
      width: 30px;
      height: 30px;
      background: url(${arr}) no-repeat center;
    }
  }

  .filter__select__indicator-separator {
    display: none;
  }

  .filter__select__option {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.5;
    padding: 10px;
    &:active {
      background-color: #EEF4FF;
    }
  }

  .filter__select__option--is-selected {
    background-color: #EEF4FF;
    color: black;
  }

  .filter__select__single-value {
    color: #8B8B8B;
  }

  input[type="text"] {
    font-family: 'Proxima Nova';
    font-size: 100%;
    line-height: 1.8;
    padding: 9px;
    display: inline-block;
    padding-right: 30px;
    border: 1px solid #D8D8D8;
    outline: none;
  }
`;

const SelectWrap = styled.div`
  margin-bottom: 20px;
  margin-right: 20px;
  &:last-child {
    margin-right: 0;
  }
`;


const SearchWrap = styled.div`
  display: inline-block;
  margin-right: 20px;
  position: relative;

  &:after {
    content: '';
    display: block;
    position: absolute;
    width: 28px;
    height: 28px;
    background: url(${search}) no-repeat center;
    right: 6px;
    top: 10px;
  }
`;





function FiltersSelectPrograms(props) {
      const { setProgramId } = props;

      return (
        <Wrap>
          <StyledFiltersSelect>
              <div className="filter__showed">
                <SelectWrap>
                    <Select onChange={(e) => setProgramId(e.value)} defaultValue={options[0]} classNamePrefix={'filter__select'} options={options} />
                </SelectWrap>
                <SelectWrap>
                    <Select defaultValue={optionsyear[0]} classNamePrefix={'filter__select'} options={optionsyear} />
                </SelectWrap>
                <SelectWrap>
                  <SearchWrap>
                    <input type="text" name="search" placeholder="Поиск" />
                  </SearchWrap>
                  <BlueButton />
                </SelectWrap>
              </div>
          </StyledFiltersSelect>
        </Wrap>
      );
  }
  


FiltersSelectPrograms.propTypes = {
}


const mapStateToProps = store => {
    return {
      //meridian: store.meridian,
      //center: store.countryListByCoordinate.coordinates[1],
      //viewMode: store.mapMode.mode
    }
  }

  const mapDispatchToProps = dispatch => {
    return {
      setProgramId: (id) => dispatch(setProgramId(id)),
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(FiltersSelectPrograms)



