import urlApi from './urlApi'
export const AUTH_ME_REQUEST = 'AUTH_ME_REQUEST'
export const AUTH_ME_SUCCESS = 'AUTH_ME_SUCCESS'
export const AUTH_ME_ERROR = 'AUTH_ME_ERROR'

export function authMe(token) {


  return (dispatch) => {

    dispatch({
      type: AUTH_ME_REQUEST,
      payload: true,
    })
  
    urlApi().authme(token).then(res => {
        dispatch({
          type: AUTH_ME_SUCCESS,
          payload: res,
        })
    })
    .catch(err => {
      dispatch({
        type: AUTH_ME_ERROR,
        payload: err
      })
    })
  }
}


