const range = len => {
    const arr = []
    for (let i = 0; i < len; i++) {
      arr.push(i)
    }
    return arr
  }
  
  const newPerson = id => {
    const statusChance = Math.random()
    return {
        id,
        num: 1,
        groupName: 'Константин Константинопольсий',
        year: '2019/2020',
        course: Math.floor(Math.random() * 5),
        curatorName: 'Константин Константинопольсий',
        discipline: 'Школа дизайна',
        faculty: 'Гейм-дизайн и виртуальная реальность',
        status:  '- / - /' 
    }
  }
  
  export default function makeData(...lens) {
    const makeDataLevel = (depth = 0) => {
      const len = lens[depth]
      return range(len).map(d => {
        return {
          ...newPerson(d),
          subRows: lens[depth + 1] ? makeDataLevel(depth + 1) : undefined,
        }
      })
    }
  
    return makeDataLevel()
  }


