import React, { useState, useEffect } from 'react';
import Table from '../tables/main'
import { IndeterminateCheckbox } from '../tables/checkbox'
import makeData from '../tables/makeData'
import { connect } from 'react-redux';
import styled from "styled-components";
import FiltersSelectPrograms from '../filters/programskkk'
import BlueButton from '../tables/blueButton';
import { getPrograms } from '../../actions/getprograms'


const TableWrapper = styled.section`
  margin-top: 40px;

  table {
    border-spacing: 0;
    width: 100%;
    }

    tbody {
      ${'' /* These styles are required for a scrollable table body */}
      overflow-y: scroll;
      overflow-x: hidden;
      height: 250px;
    }
  }

  .testing {
    label {
      display: inline-block;
      margin-right: 20px;
    }
  }
`;

function ProgramsPage(props) {
    const { smOpened, programs, getPrograms } = props;
  
    const [sort, setSort] = useState(false);
    const [dnd, setDnd] = useState(false);
    const [select, setSelect] = useState(false);
  
    const columns = React.useMemo(
      () => [
            {
              Header: ({ getToggleAllRowsSelectedProps }) => (
                <div className="td__select">
                  <label>
                    <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
                    <i></i>
                  </label>
                </div>
              ),  
              accessor: 'id',
              className: 'width__test',
              width: 90
            },
            {
              Header: 'Программа',
              accessor: 'baseProgramName',
              width: 250
            },
            {
              Header: 'Подпрограмма',
              accessor: 'name',
              width: 250
            },
            {
              Header: 'Учебный год',
              accessor: 'AcademicYear',
            },
            {
              Header: 'Тип',
              accessor: 'learningType',
            },
            {
              Header: 'Скрытая',
              accessor: 'isHide',
            },
            {
              Header: '',
              accessor: 'copy',
            },
            {
              Header: '',
              accessor: 'edit',
            },    
      ],
      []
    )
  
    const data = programs.data.map(item => {
      if (item.isHide) {
        return { ...item, isHide: 'Да'}
      }
      return { ...item, isHide: 'Нет'}
    })

    useEffect(() => {
      getPrograms(5);
    }, [])

    
    return (
      <TableWrapper>
        <div className="container-fluid">
          <h1>Программы</h1>
          <FiltersSelectPrograms />
          <div className="testing">
            <label>
              <input type="checkbox" checked={sort} onChange={() => setSort(!sort)}/>
              Сортировка
            </label>
            <label>
              <input type="checkbox" checked={select} onChange={() => setSelect(!select)}/>
              Выделение
            </label>
            <label>
              <input type="checkbox" checked={dnd} onChange={() => setDnd(!dnd)}/>
              ДнД
            </label>
          </div>
          <div>
          </div>
          <Table 
            key={Date.now()}
            columns={columns} 
            data={data} 
            smOpened={smOpened} 
            sorting={sort}
            select={select}
            dnd={dnd}
          />
        </div>
      </TableWrapper>
    )
  }
  
  
  
  ProgramsPage.propTypes = {
  
  }
  
  
  
  const mapStateToProps = store => {
    return {
      programs: store.programskkk,
      smOpened: store.togglesidemenu.isOpened,
    }
  }

  const mapDispatchToProps = dispatch => {
    return {
      getPrograms: (programGroupId) => dispatch(getPrograms(programGroupId)),
    }
  }
  
  
  export default connect(mapStateToProps, mapDispatchToProps)(ProgramsPage)


