import React from 'react';
import styled from "styled-components";

const StyledButton = styled.div`
    display: inline-block;
    font-family: Proxima Nova;
    font-style: normal;
    font-weight: normal;
    font-size: 110%;
    line-height: 1.6;
    background: #004CBA;
    cursor: pointer;
    padding: 11px 50px;
    color: white;
`

export default function BlueButton(props) {
    return (
        <StyledButton>
            Добавить
        </StyledButton>
    )
}


