import React, { useState } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import edit from '../../images/icons/edit.svg';
import profile from '../../images/icons/profile_circle.svg';
import more from '../../images/icons/more.svg';
import left from '../../images/icons/left.svg'
import { toggleSideMenu } from '../../actions/togglesidemenu'
import { useHistory } from "react-router-dom";
import withBreadcrumbs from 'react-router-breadcrumbs-hoc';
import { listItemsMain } from '../../constants/mainpagemenu';
import { logout } from '../../actions/logout'


const HeaderWrapper = styled.section`
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    border-bottom: 1px solid #D8D8D8;
    position: relative;
`;

const Breadcrumbs = styled.div`
    padding-left: ${props => props.isOpened ? '80px' : '20px'};
    padding-top: 4px;
    line-height: 1.5;
    flex: 1;

    span {
      display: inline-block;
      position: relative;
      margin-right: 15px;
      padding-right: 15px;
      &:after {
        position: absolute;
        right: -5px;
        display: inline-block;
        content: '/';
      }

      &:last-child {
        &:after {
          display: none;
        }
      }
    }
`;

const Edit = styled.div`
  width: 60px;
  height: 60px;
  border-left: 1px solid #D8D8D8;
  cursor: pointer;
  background: url(${edit}) no-repeat center;
  transition: 0.2s;
  &:hover {
    background-color: #D8D8D8;
  }
`;

const Profile = styled.div`
  width: 60px;
  height: 60px;
  border-left: 1px solid #D8D8D8;
  cursor: pointer;
  background: url(${profile}) no-repeat center;
  transition: 0.2s;
  &:hover {
    background-color: #D8D8D8;
  }
`;

const More = styled.div`
  width: 60px;
  height: 60px;
  border-left: 1px solid #D8D8D8;
  cursor: pointer;
  background: url(${more}) no-repeat center;
  transition: 0.2s;
  &:hover {
    background-color: #D8D8D8;
  }
`;


const Open = styled.div`
  display: ${props => props.isOpened ? 'none' : 'block'};
  cursor: pointer;
  width: 60px;
  height: 60px;
  background: url(${left}) no-repeat center;
  transform: rotate(180deg);
  border-left: 1px solid #D8D8D8;
`


const MoreMenu = styled.div`
  display: ${props => props.isOpened ? 'block' : 'none'}
  position: absolute;
  background: #FFFFFF;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  right: 0px;
  top: 60px;
  z-index: 15;
  min-width: 320px;
  ul {
    li {
      display: block;
      padding: 20px 30px;
      cursor: pointer;
      transition: 0.2s;
      border-bottom: 1px solid #D1D1D1;
      font-family: 'Proxima Nova';
      font-style: normal;
      font-weight: normal;
      font-size: 120%;
      line-height: 1.2;
      color: #8B8B8B;
      &:hover {
        background: #F0F0F0;
      }

      &:last-child {
        border-bottom: none;
      }

      &.active {
        color: #17181A;
        background: #F0F0F0;
      }
    }
  }
`



/*

динамические роуты, пока не нужны

const DynamicUserBreadcrumb = ({ match }) => {
  let filteredList = listItemsMain.filter((el) => el.link == match.params.userId);
  return <span>{filteredList[0].name}</span>
};

const routes = [{ path: "/main/:userId", breadcrumb: DynamicUserBreadcrumb }];
*/


/*хлебные крошки и кнопки надо раскидать по компонентам как будет более понятен функционал*/

const Breadcrumbs1 = withBreadcrumbs()(({ breadcrumbs }) => (
  <React.Fragment>
    {breadcrumbs.map(({ breadcrumb }, i) => i === 0 ? '' : breadcrumb)}
  </React.Fragment>
));



function Header(props) {
  const { smOpened, toggleSideMenu, logout } = props;
  const [ moremenu, toggleMoreMenu ] = useState(false);

  const history = useHistory();

  function logoutClick() {
    history.push("/login");
    logout();
  }

  return (
    <HeaderWrapper>
      <Open onClick={() => toggleSideMenu(true)} isOpened={smOpened}>

      </Open>
       <Breadcrumbs isOpened={smOpened}>
          <Breadcrumbs1 />
       </Breadcrumbs>
        <Edit />
        <Profile />
        <More onClick={() => toggleMoreMenu(!moremenu)}/>
        <MoreMenu isOpened={moremenu}>
          <ul>
            <li onClick={() => logoutClick()}>
              Выйти
            </li>
            <li>
              Тест
            </li>
          </ul>
        </MoreMenu>
    </HeaderWrapper>
  );
}



Header.propTypes = {

}


const mapStateToProps = store => {
  return {
    smOpened: store.togglesidemenu.isOpened,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleSideMenu: (isOpened) => dispatch(toggleSideMenu(isOpened)),
    logout: () => dispatch(logout())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Header)


