import React from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import SideMenuHeader from './header'
import MenuItem from './menuitem';
import { listItemsKKK } from '../../constants/kkkpagemenu'


const SideMenuOpenedWrapper = styled.section`
  display: ${props => props.isOpened ? 'block' : 'none'}
  background: #F0F0F0;
  padding-top: 15px;
  padding-bottom: 20px;
  min-height: 100vh;
  width: 320px;
`;

const InnerWrapper = styled.div`
  .title__inner {
      margin-bottom: 20px;
  }

  .main__list {
    padding-left: 20px;
    > ul > li {
      margin-bottom: 20px;
    }
  }
`;


function SideMenuKKK(props) {
  const { smOpened } = props;


  return (
    <SideMenuOpenedWrapper isOpened={smOpened}>
        <SideMenuHeader name={'KKK'} activeEl={'kkk'} />
        <MenuItem title={'KKK'} list={listItemsKKK} isInner={false}/>
    </SideMenuOpenedWrapper>
  );
}


SideMenuKKK.propTypes = {

}



const mapStateToProps = store => {
  return {
    smOpened: store.togglesidemenu.isOpened,
  }
}


export default connect(mapStateToProps)(SideMenuKKK)


