import { combineReducers } from 'redux'
import { authLoginReducer } from './authlogin'
import { authMeReducer } from './authme'
import { togglesidemenuReducer } from './toggleSideMenu'
import { getProgramsReducer } from './getprograms'


export const rootReducer = combineReducers({
    auth: authLoginReducer,
    authinfo: authMeReducer,
    togglesidemenu: togglesidemenuReducer,
    programskkk: getProgramsReducer
});




