import React, { useState } from 'react';
import Table from '../tables/main'
import { IndeterminateCheckbox } from '../tables/checkbox'
import makeData from '../tables/makeData'
import { connect } from 'react-redux';
import styled from "styled-components";
import FiltersSelect from '../filters/main'


const TableWrapper = styled.section`
  margin-top: 40px;

  table {
    border-spacing: 0;
    width: 100%;
    }

    tbody {
      ${'' /* These styles are required for a scrollable table body */}
      overflow-y: scroll;
      overflow-x: hidden;
      height: 250px;
    }
  }

  .testing {
    label {
      display: inline-block;
      margin-right: 20px;
    }
  }

`;

function EducationPage(props) {
    const { smOpened } = props;
  
    const [sort, setSort] = useState(false);
    const [dnd, setDnd] = useState(false);
    const [select, setSelect] = useState(false);
  
    const columns = React.useMemo(
      () => [
            {
              Header: ({ getToggleAllRowsSelectedProps }) => (
                <div className="td__select">
                  <label>
                    <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
                    <i></i>
                  </label>
                </div>
              ),  
              accessor: 'id',
              sticky: "left",
              width: 144,
              totalWidth: 144,
              totalLeft2: 0
            },
            {
              Header: 'Название',
              accessor: 'groupName',
              sticky: "left",
              width: 144,
              totalWidth: 144,
              totalLeft2: 144
            },
            {
              Header: 'Учебный год',
              accessor: 'year',
              width: 170,
            }, 
            {
              Header: 'Курс',
              accessor: 'course',
              width: 250,

            },
            {
              Header: 'Куратор',
              accessor: 'curatorName',
              width: 250,
            },
            {
              Header: 'Дисциплина',
              accessor: 'discipline',
              width: 250,
            }, 
            {
              Header: 'Факультет',
              accessor: 'faculty',
              width: 250,
            },
            {
              Header: 'Посещаемость',
              accessor: 'status',
              width: 250,
            },
      ],
      []
    )
  
    const data = React.useMemo(() => makeData(20), [])
    
    return (
      <TableWrapper>
        <div className="container-fluid">
          <h1>Таблица и фильтр</h1>
          <FiltersSelect />
          <div className="testing">
            <label>
              <input type="checkbox" checked={sort} onChange={() => setSort(!sort)}/>
              Сортировка
            </label>
            <label>
              <input type="checkbox" checked={select} onChange={() => setSelect(!select)}/>
              Выделение
            </label>
            <label>
              <input type="checkbox" checked={dnd} onChange={() => setDnd(!dnd)}/>
              ДнД
            </label>
          </div>
          <Table 
            columns={columns} 
            data={data} 
            smOpened={smOpened} 
            sorting={sort}
            select={select}
            dnd={dnd}
          />
        </div>
      </TableWrapper>
    )
  }
  
  
  
  EducationPage.propTypes = {
  
  }
  
  
  
  const mapStateToProps = store => {
    return {
      smOpened: store.togglesidemenu.isOpened,
    }
  }
  
  
  export default connect(mapStateToProps)(EducationPage)


