import React from "react";
import { Route, Redirect } from "react-router-dom";


const ProtectedRoute = ({
    component: Component,
    isAuthenticated,
    isVerifying,
    ...rest
  }) => (
    <Route
      {...rest}
      render={props =>
        isVerifying ? (
          <div style={{ 
            position: 'fixed', 
            top: '0', 
            bottom: '0',
            left: 0, 
            right: 0, 
            paddingTop:'40vh', 
            textAlign: 'center'
          }}> Загрузка </div>
        ) : isAuthenticated ? (
          <Component {...props} />
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: props.location }
            }}
          />
        )
      }
    />
  );




  export default ProtectedRoute;


