import { GET_PROGRAMS_REQUEST, GET_PROGRAMS_SUCCESS, GET_PROGRAMS_ERROR } from '../actions/getprograms'
import { SET_PROGRAM_ID } from '../actions/setProgramId'

const initialState = { 
    isLoading: false,
    isError: false, 
    programId: 5,
    data: []
};


  
export function getProgramsReducer(state = initialState, action) {
    switch(action.type) {
        case GET_PROGRAMS_REQUEST:
            return { ...state, isLoading: true, isError: false }

        case GET_PROGRAMS_SUCCESS:
            return { ...state, isLoading: false, isError: false, data: action.payload.data }
    
        case GET_PROGRAMS_ERROR:
            return { ...state, isLoading: false, isError: true, data: [] }
        
        case SET_PROGRAM_ID: 
            return { ...state, programId: action.payload }

        default:
            return state;
    }
};


