import React from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import SideMenuHeader from './header'
import MenuItem from './menuitem';
import { listItemsMain, listItemsTeacher, listItemsProjects, listItemsAdmin } from '../../constants/mainpagemenu'
import ItemInner from './menuiteminner'


const SideMenuOpenedWrapper = styled.section`
  display: ${props => props.isOpened ? 'block' : 'none'}
  background: #F0F0F0;
  padding-top: 15px;
  padding-bottom: 20px;
  min-height: 100vh;
  width: 320px;
`;

const InnerWrapper = styled.div`
  .title__inner {
      margin-bottom: 20px;
  }

  .main__list {
    padding-left: 20px;
    > ul > li {
      margin-bottom: 20px;
    }
  }
`;


function SideMenu(props) {
  const { smOpened } = props;


  return (
    <SideMenuOpenedWrapper isOpened={smOpened}>
        <SideMenuHeader name={'Timetracker'} activeEl={'main'} />
        <MenuItem title={'Основные'} list={listItemsMain} isInner={false}/>
        <MenuItem title={'Преподаватель'} list={listItemsTeacher} isInner={false}/>
        <MenuItem title={'Учебный офис'} list={listItemsProjects} isInner={false}/>
        <ItemInner title={'Администратор'} list={listItemsAdmin} />
    </SideMenuOpenedWrapper>
  );
}


SideMenu.propTypes = {

}



const mapStateToProps = store => {
  return {
    smOpened: store.togglesidemenu.isOpened,
  }
}


export default connect(mapStateToProps)(SideMenu)


