import React, { useState } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import MenuItem from './menuitem';
import { Title, List } from './menuitem';



const InnerWrapper = styled.div`
  .title__inner {
      margin-bottom: 20px;
  }

  .main__list {
    padding-left: 20px;
    > ul > li {
      margin-bottom: 20px;
    }
  }
`;



function ItemInner(props) {
  const { list, title } = props;
  const [opened, setOpened] = useState(true);

  const innerList = list.map((item, i) => 
    <li key={i}><MenuItem title={item.name} list={item.links} isInner={true}/></li>
  )

  return(
    <InnerWrapper>
      <Title className="title__inner" onClick={() => setOpened(!opened)} opened={opened}>
        {title}
      </Title>
      <List className="main__list"  opened={opened}>
        <ul>
            {innerList}
        </ul>
      </List>
    </InnerWrapper>
  )
}



ItemInner.propTypes = {

}



const mapStateToProps = store => {
  return {
  }
}


export default connect(mapStateToProps)(ItemInner)


