import { TOGGLE_SIDE_MENU } from '../actions/togglesidemenu'

const initialState = {  
    isOpened: true
};


  
export function togglesidemenuReducer(state = initialState, action) {
    switch(action.type) {
        case TOGGLE_SIDE_MENU:
            return { ...state, isOpened: action.payload }

        default:
            return state;
    }
};


