import { AUTH_LOGIN_REQUEST, AUTH_LOGIN_SUCCESS, AUTH_LOGIN_ERROR } from '../actions/authlogin'

const initialState = { 
    isLoading: false,
    isError: false, 
    data: {
        access_token: '',
        resultCode: 100
    }
};


  
export function authLoginReducer(state = initialState, action) {
    switch(action.type) {
        case AUTH_LOGIN_REQUEST:
            return { ...state, isLoading: true, isError: false }

        case AUTH_LOGIN_SUCCESS:
            return { ...state, isLoading: false, isError: false, data: action.payload.data }
    
        case AUTH_LOGIN_ERROR:
            return { ...state, isLoading: false, isError: true, data: { resultCode: 100 } }

        default:
            return state;
    }
};


