import axios, { post } from 'axios'

export default function urlApi() {
    const baseUrl = process.env.REACT_APP_API_URL;
    const baseQueryLink = 'https://api.design.hse.ru/v1.2';
    return {

        authlogin(login, password) {
            return axios.get(`${baseQueryLink}/auth/token?login=${login}&password=${password}`)
        },

        authme(token) {
            return axios.get(`${baseQueryLink}/auth/me`, { headers: { token: token }})
        },

        getPrograms(programGroupId) {
            return axios.get(`${baseQueryLink}/programs?programGroupId=${programGroupId}`)
        }

        /*

        authlogin(login, password) {
            return post(`${baseQueryLink}/`, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            })
        }

        */
    }
}


