import React, { useState } from 'react';
import { connect } from 'react-redux';
import styled from "styled-components";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import logo from '../../images/icons/hse_blue.png'
import bottom from '../../images/icons/bottom.svg'
import left from '../../images/icons/left.svg'
import check from '../../images/icons/check.svg'
import { toggleSideMenu } from '../../actions/togglesidemenu'

const Header = styled.section`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
  border-bottom: 1px solid #D1D1D1;
  margin-bottom: 60px;
`;

const Logo = styled.div`
  margin-right: 15px;
`;

const TitleMenu = styled.div`
  flex: 1;
  position: relative;
  span {
    display: inline-block;
    position: relative;
    font-size: 125%;
    font-family: 'Proxima Nova';
    line-height: 1.2;
    color: #17181A;
    font-weight: bold;
    transition: 0.2s;
    cursor: pointer;
    &:after {
      content: '';
      display: inline-block;
      position: absolute;
      background: url(${bottom}) no-repeat center;
      right: -20px;
      width: 12px;
      height: 12px;
      top: calc(50% - 6px);
    }
  }
`;

const Close = styled.div`
  width: 25px;
  height: 20px;
  margin-left: 20px;
  background: url(${left}) no-repeat center;
  cursor: pointer;
`;


const HeaderMenu = styled.div`
  display: ${props => props.opened ? 'block' : 'none'};
  position: absolute;
  background: #FFFFFF;
  top: 42px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  width: 320px;
  z-index: 3;
  a {
    display: block;
    position: relative;
    font-size: 125%;
    line-height: 1.2;
    font-family: 'Proxima Nova';
    padding: 23px 30px;
    border-bottom: 1px solid #D1D1D1;
    transition: 0.2s;
    color: #17181A;
    &:last-child {
      border-bottom: none;
    }
    &:hover {
      background: #D8D8D8;
      color: #17181A;
    }

    &.${props => props.activeEl} {
      &:after {
        content: '';
        display: block;
        position: absolute;
        width: 24px;
        height: 24px;
        background: url(${check}) no-repeat center;
        right: 20px;
        top: calc(50% - 12px);
      }
    }
  }
`



function SideMenuHeader(props) {
  const [opened, setOpened] = useState(false);
  const { smOpened, toggleSideMenu, name, activeEl } = props;
  
  return (
    <Header>
      <Logo>
        <a href="#">
          <img src={logo} alt="logo" />
        </a>
      </Logo>
      <TitleMenu>
        <span onClick={() => setOpened(!opened)}>
          {name}
        </span>
        <HeaderMenu activeEl={activeEl} opened={opened}>
          <Link className="main" to="/main">Timetracker</Link>
          <Link to="/main">Олимпиада</Link>
          <Link className="kkk" to="/kkk">ККК</Link>
        </HeaderMenu>
      </TitleMenu>
      <Close onClick={() => toggleSideMenu(false)}>

      </Close>
    </Header>
  );
}



SideMenuHeader.propTypes = {

}



const mapStateToProps = store => {
  return {
    smOpened: store.togglesidemenu.isOpened,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    toggleSideMenu: (isOpened) => dispatch(toggleSideMenu(isOpened)),
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(SideMenuHeader)


