import { AUTH_ME_REQUEST, AUTH_ME_SUCCESS, AUTH_ME_ERROR } from '../actions/authme'
import { LOGOUT } from '../actions/logout'

const initialState = { 
    isLoading: false,
    status: 0,
    data: {
    }
};


  
export function authMeReducer(state = initialState, action) {
    switch(action.type) {
        case AUTH_ME_REQUEST:
            return { ...state, isLoading: true, status: 0 }

        case AUTH_ME_SUCCESS:
            return { ...state, isLoading: false, data: action.payload.data, status: action.payload.status }
    
        case AUTH_ME_ERROR:
            return { ...state, isLoading: false, data: action.payload.data, status: action.payload.status  }
        
        case LOGOUT:
            return { ...state, isLoading: false, data: { }, status: 0 }

        default:
            return state;
    }
};


